import { Component } from '@angular/core';
import { IonicPage, LoadingController, ModalController, NavParams, ViewController } from 'ionic-angular';
import { UserMapper } from '../../../models/user.model';
import { User } from '../../../providers';
import { AnalytictsTrack } from '../../../decorators/analyticts-track.decorators';
import { ToastProvider } from '../../../providers/toast/toast';
import { StatesProvider } from '../../../providers/states/states';
import { SpecializationsProvider } from '../../../providers/specializations/specializations';
import { ColorPickPage } from '../../index';

/**
 * Generated class for the EditProfilePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@AnalytictsTrack('[PERFIL] Editar perfil')
@IonicPage()
@Component({
  selector: 'page-edit-profile',
  templateUrl: 'edit-profile.html',
})
export class EditProfilePage {

  loggedUser: UserMapper;

  states: any[];

  cities: any[];

  specializations: any[];

  allowEdition: boolean = true;

  specialization: any;

  user: UserMapper;

  constructor(private userPrv: User, private toastPrv: ToastProvider, private modalCtrl: ModalController,
              public navParams: NavParams, private statePrv: StatesProvider, public viewCtrl: ViewController,
              private specializationPrv: SpecializationsProvider, private loadingCtrl: LoadingController) {
    this.user = this.navParams.get('user');
    this.specialization = this.user.specialization ? this.user.specialization : null;
  }

  ionViewDidLoad() {
    this.getLoggedUser();
    this.loadStates();
    this.loadCities();
    this.loadSpecializations();
  }

  async getLoggedUser() {
    this.loggedUser = await this.userPrv.user();
  }

  save() {

    let loading = this.loadingCtrl.create({
      content: 'Salvando seus dados...'
    });

    loading.present();

    this.user.terms_read = 1;
    this.userPrv.save(this.user).subscribe((data: any) => {
      this.userPrv.setUser(data.data);
      this.user = data.data;
      this.toastPrv.show('Seus dados foram salvos com sucesso');
      loading.dismiss();
      this.dismiss();
    });
  }

  loadStates() {
    let loading = this.loadingCtrl.create({
      content: 'Carregando estados...'
    });

    loading.present();
    this.statePrv.query().subscribe((data) => {
      this.states = data;
      loading.dismiss();
    });
  }

  loadCities() {
    if (this.user && this.user.state_id) {
      let loading = this.loadingCtrl.create({
        content: 'Carregando cidades...'
      });

      loading.present();
      this.statePrv.cities(this.user.state_id).subscribe((data) => {
        this.cities = data;
        loading.dismiss();
      });
    }
  }

  loadSpecializations() {
    this.specializationPrv.query().subscribe((data) => {
      this.specializations = data;
    });
  }

  chooseAColor() {
    let colorModal = this.modalCtrl.create(ColorPickPage, { color: this.user.professional.color });
    colorModal.onDidDismiss(data => {
      this.user.professional.color = data.color;
    });
    colorModal.present();
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }
}



